
import 'bootstrap/dist/css/bootstrap.min.css';
import React, { useState } from 'react';
import Button from "react-bootstrap/Button";
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';

 //React Bootstrap
 <link
  rel="stylesheet"
  href="https://cdn.jsdelivr.net/npm/bootstrap@5.3.0/dist/css/bootstrap.min.css"
  integrity="sha384-9ndCyUaIbzAi2FUVXJi0CjmCapSmO7SnpJef0486qhLnuZ2cdeRhO02iuK6FUUVM"
  crossorigin="anonymous"
/>//React Bootstrap


const SearchBar = ({ handleSearch, setSearchTerm }) => {

  const [term, setTerm] = useState('');
  const [minPrice, setMinPrice] = useState('');
  const [maxPrice, setMaxPrice] = useState('');
  const [type, setType] = useState('');
  const [minBeds, setMinBeds] = useState('');
  const [maxBeds, setMaxBeds] = useState('');
  const [dateAddedBefore, setDateAddedBefore] = useState('');
  const [dateAddedAfter, setDateAddedAfter] = useState('');
  const [postcodeArea, setPostcodeArea] = useState('');
  const [purchBtn, setPurchBtn] = useState('');


  const handleSubmit = (e) => {//submits all the values to handleSearch in App.js
    e.preventDefault(); //prevents form from being submitted without button
    handleSearch({
      term, //error troubleshooting, term to lowecase not a function
      minPrice,
      maxPrice,
      type,
      minBeds,
      maxBeds,
      dateAddedBefore,
      dateAddedAfter,
      postcodeArea,
      purchBtn,
    });
  };
  return (
    <section className='search-container'>
      
      <h3>
        Find your dream home [rent or sale]
      </h3>
      <form onSubmit={handleSubmit}>{/* submit all form values */}
        

        {/* used bootstrap to improve form and structure */}
        <Form.Group className="mb-3" controlId="formSearch">
          <Form.Label htmlFor="term">Search Gadget or Location</Form.Label>
        <Form.Control type="text" id="term" name="term" 
        placeholder="Search Homes..." value={term}
        onChange={(e) => setTerm(e.target.value)}/>
        </Form.Group>
        
       
        <Form.Group className="mb-3" controlId="buttons">
        <Button variant='primary' size='lg' type='submit' id="buttonSearch" name="sale" value={purchBtn} onClick={() => setPurchBtn("sale")}>For Sale</Button>{' '}
        <Button variant='primary' size='lg' type='submit' id="buttonSearch" name="rent" value={purchBtn} onClick={() => setPurchBtn("rent")}>For Rent</Button>{' '}
        </Form.Group>{/* Buttons submit form while providing own data sale or rent to further filter the results */}
        

        <Form.Group className="mb-3" controlId="priceNumber">
        <Form.Label htmlFor=""><h5>Price:</h5></Form.Label> 
        
        {/* bootstrap row - col structure */}
        <Row>
          
          <Col>
            <Form.Control
              type="number"
              id="minPrice"
              name="minPrice"
              placeholder="Minimum Price "
              value={minPrice}
              onChange={(e) => setMinPrice(e.target.value)}>
              </Form.Control> 
              {/* set minPrice to value of input  */}
          </Col>

          <Col>
            
            <Form.Control
              type="number"
              id="maxPrice"
              name="maxPrice"
              placeholder="Maximum Price "
              value={maxPrice}
              onChange={(e) => setMaxPrice(e.target.value)}>
                {/* //set maxPrice to value of input */}
              </Form.Control>
          </Col>
        </Row>
        </Form.Group>
        

        <Form.Label htmlFor=""><h5>Type:</h5></Form.Label>
        <Form.Select
          name="type"
          id="type"
          value={type}
          onChange={(e) => setType(e.target.value)}
        > 
          
          <option value="house">House</option>
          <option value="Flat">Flat</option>
          <option value="Any">Any</option>
        </Form.Select>
        
      <br />

      <Form.Group className="mb-3" controlId="bedNumber">
        <Form.Label htmlFor=""><h5>Beds:</h5></Form.Label>
        <br />

        <Row>
        <Col>
          <Form.Select name="min bedroom" id="minBeds" placeholder='Min Beds' value={minBeds} 
          onChange={(e) => setMinBeds(e.target.value)}>
            <option>Min</option>
            <option value="1">1</option>
            <option value="2">2</option>
            <option value="3">3</option>
            <option value="4">4</option>
            <option value="5">5</option>
            <option value="6">6</option>
            <option value="7">7</option>
            <option value="8">8</option>
            <option value="9">9</option>
          </Form.Select>
        </Col>
        <Col>
        <Form.Select name="max bedroom" id="maxBeds" placeholder='Max Beds' value={maxBeds}
          onChange={(e) => setMaxBeds(e.target.value)}>
          <option>Max</option>
          <option value="1">1</option>
          <option value="2">2</option>
          <option value="3">3</option>
          <option value="4">4</option>
          <option value="5">5</option>
          <option value="6">6</option>
          <option value="7">7</option>
          <option value="8">8</option>
          <option value="9">9</option>
        </Form.Select>
        </Col>
        </Row>
        </Form.Group>
        <br />
        
        <Form.Group className="mb-3" controlId="date">
        <Form.Label><h5>Date Added:</h5>
        </Form.Label> <br />

        <Row>
          <Col>
        <Form.Label htmlFor=""> After: </Form.Label>
        <Form.Control type="date" name="dateAddedAfter" placeholder='After...' value={dateAddedAfter}
          onChange={(e) => setDateAddedAfter(e.target.value)}/>
          </Col>
          <Col>
        <Form.Label htmlFor=""> Before: </Form.Label>
        <Form.Control type="date" name="dateAddedBefore" placeholder='Before...' value={dateAddedBefore}
          onChange={(e) => setDateAddedBefore(e.target.value)}/>
          </Col>
          </Row>
        </Form.Group>

        <br />

        <Form.Group className="mb-3" controlId="date">
        <Form.Label>
        <h5>Postcode Area:</h5></Form.Label> <br />
        <Form.Control type="text" name="postcodeArea" value={postcodeArea}
          onChange={(e) => setPostcodeArea(e.target.value)}/>
        </Form.Group>{/* for control is input */}
        </form>   
    </section>
  );
};

export default SearchBar;
