import React, { useEffect, useState } from 'react';
import FavoriteList from './FavoriteList';
import ImageCard from './ImageCard';

const Gallery = ({ properties }) => {
  const [favorites, setFavorites] = useState([]);


  const handleAddToFavorites = (property) => {// This function is passed to ImageCard

    const isAlreadyInFavorites = favorites.some((favProperty) => favProperty.id === property.id);//check property id against favProperty ids to see if already in favorites
    if (isAlreadyInFavorites) {
      return;//if already in favorites, do nothing
    }else{
    setFavorites([...favorites, property]);}//if not already in favorites, add to favorites
  };  

  const handleRemoveFromFavorites = (property) => {
    // Filter out the property with the property id
    const updatedFavorites = favorites.filter((favProperty) => favProperty.id !== property.id);
  
    // Update the favorites state with the filtered array
    setFavorites(updatedFavorites);
  };

  const handleRemoveAllFromFavorites = () => {
    const updatedFavorites = [];
    setFavorites(updatedFavorites); //set favorites to empty array
  }
  

  useEffect(() => {
    // Save favorites to local storage whenever it changes
    localStorage.setItem('favorites', JSON.stringify(favorites));
  }, [favorites]);

  useEffect(() => {
    // Load favorites from local storage on component mount
    const savedFavorites = localStorage.getItem('favorites');
    if (savedFavorites) {
      setFavorites(JSON.parse(savedFavorites));
    }
  }, []);

  const [droppedItem, setDroppedItem] = useState(null);

 

  return (
    
    <div className="container">
        <div className="all-items">
            <h2>Available</h2>
            <div className="gallery">{/* map properties to propery and create an image card for each property*/}
            {properties.map((property) => (
              
                <ImageCard key={property.id} property={property} handleAddToFavorites={handleAddToFavorites} handleRemoveFromFavorites={handleRemoveFromFavorites} favorites={favorites}/>
                
                  ))}
            </div>
        </div>

        
        <div className="favorites"> {/* Calls favorites and passes necesarry functions and variables */}
        
        <FavoriteList favorites={favorites} handleAddToFavorites={handleAddToFavorites} handleRemoveFromFavorites={handleRemoveFromFavorites} handleRemoveAllFromFavorites={handleRemoveAllFromFavorites}/>
        
        </div>
        
    </div>
    
  );
};

export default Gallery;
