import React from 'react';
import { Link } from 'react-router-dom';

const ImageCard = ({ property, handleAddToFavorites, handleRemoveFromFavorites, favorites }) => {
  const { type, purchaseType, bedrooms, location, added, picture, price, postcode } = property;
  const firstImage = picture[0];
  const imageName = firstImage.split('/').pop(); // Assuming the path is in the format "images/imagename.jpg"

  const isPropertyInFavorites = favorites.some((favProperty) => favProperty.id === property.id); //check property id against favProperty ids
  const isPropertyRent = property.purchaseType.toLowerCase() === "rent";

  return (
    
    <section className="card">
      
      
        <div className='image'>
        <img src={firstImage} alt={imageName} />{/* Sets image src */}
        </div>
        
      <div className="description"> {/* accessing property variables */}
        <h3>Type: {type}</h3>
        <h3>Bedrooms: {bedrooms}</h3>
        <h3>Location: {location}</h3>
        <h3>Postcode: {postcode}</h3>
        <h3>Added: {added.month} {added.day}, {added.year}</h3>
        {/* if renting print price with pcm, else print price without */}
        {isPropertyRent ? ( 
          <h2>Price: {"" + price + " pcm"} </h2>
        ) : (
          <h2>Price: {"" + price} </h2>
        )}

        {/* if in favorites display remove button, else display add to favorites */}
        {isPropertyInFavorites ? (
          <button onClick={() => handleRemoveFromFavorites(property)}>Remove From Favorites</button>
          ) : (
          <button onClick={() => handleAddToFavorites(property)}>Add to Favorites</button>
    )}

      <Link key={property.id} to={`/property/${property.id}`}>View More </Link>
        
      </div>
      
    </section>
    
  );
};

export default ImageCard;
