
import 'bootstrap/dist/css/bootstrap.min.css';
import React, { useEffect, useState } from 'react';
import { Route, BrowserRouter as Router, Switch } from 'react-router-dom';
import './App.css';
import Gallery from './Gallery';
import PropertyDetails from './PropertyDetails';
import SearchBar from './SearchBar';

<link
  rel="stylesheet"
  href="https://cdn.jsdelivr.net/npm/bootstrap@5.3.0/dist/css/bootstrap.min.css"
  integrity="sha384-9ndCyUaIbzAi2FUVXJi0CjmCapSmO7SnpJef0486qhLnuZ2cdeRhO02iuK6FUUVM"
  crossorigin="anonymous"
/>


function App() {
  const [properties, setProperties] = useState([]);
  const [filteredProperties, setFilteredProperties] = useState([]);
  
  

  useEffect(() => {//fetch data from properties.json on render
    const fetchData = async () => {
      const response = await fetch('/properties.json'); // Use the correct path
      const data = await response.json();
      console.log(data);
      setProperties(data.properties); //utilies the setProperties function to set the data to properties
      
      
    };

    fetchData();
    
  }, []);

  useEffect(() => {
    setFilteredProperties(properties);//set filteredProperties to properties on render as filteredProperties will be passed to Gallery
  }, [properties]);
  

  const handleSearch = (//passed all the filter values from SearchBar.js
   { term,
    minPrice,
    maxPrice,
    type,
    minBeds,
    maxBeds,
    dateAddedBefore,
    dateAddedAfter,
    postcodeArea,
    purchBtn }) => {
    
      //console.log(term); //error troubleshooting, term to lowecase not a function fixed passing to handleSearch
      let tempFilter = properties.filter((property) => { //filter properties based on search term for description
        const descriptionMatches = property.description.toLowerCase().includes(term.toLowerCase());
        const priceMatches = //if min and max price are empty then priceMatches will be true as min and max price are not within JSON file
          (!minPrice || property.price >= parseInt(minPrice, 10)) &&
          (!maxPrice || property.price <= parseInt(maxPrice, 10));


        const typeMatches = !type || property.type.toLowerCase() === type.toLowerCase();//if type is any then typeMatches will be all be true as any is not within JSON file
        if(type === 'Any'){//dont have to filter for any
        const typeMatches = !type || property.type.toLowerCase() === type.toLowerCase();} //basically always true if type is any
        

        const bedroomsMatches =  //if min and max beds are empty then bedroomsMatches will be true as min and max beds are not within JSON file
          (!minBeds || property.bedrooms >= parseInt(minBeds, 10)) &&
          (!maxBeds || property.bedrooms <= parseInt(maxBeds, 10));
        
        const dateMatches =   //if min and max date are empty then dateMatches will be true as min and max date are not within JSON file
          (!dateAddedBefore || property.added.year <= parseInt(dateAddedBefore, 10)) &&
          (!dateAddedAfter || property.added.year >= parseInt(dateAddedAfter, 10));

        const postcodeMatches = // if postcode area is not empy property postcode is filtered by postcode area
          (!postcodeArea || property.postcode.toLowerCase().includes(postcodeArea.toLowerCase()));

        const purchBtnMatches =  //if purchase type is not empty property purchase type is filtered by purchase type
          (!purchBtn || property.purchaseType.toLowerCase().includes(purchBtn.toLowerCase()));
    
        return (
          descriptionMatches &&
          priceMatches &&
          typeMatches &&
          bedroomsMatches &&
          dateMatches &&
          postcodeMatches &&
          purchBtnMatches
          
        );
      });
    
      //let tempFilter = properties.filter((property) =>
     // property.description.toLowerCase().includes(term.toLowerCase()))
      
      setFilteredProperties(tempFilter);
  };

//const filteredProperties = properties.filter((property) =>
  //  property.description.toLowerCase().includes(searchTerm.toLowerCase()));

  

    return (
      
      <Router>{/* Use the router to choose paths and create dynamic pages */}
      <div>
      <Switch>
        <Route exact path="/">
          <SearchBar handleSearch={handleSearch} />
          <Gallery properties={filteredProperties} />
        </Route>
        <Route path="/property/:propertyId"> {/* Dynamic path for each property */}
          <PropertyDetails properties={properties} />
        </Route>
      </Switch>
      </div>
    </Router>
      
      
    );
}

export default App;
