import React from 'react';

const MapComponent = ({ coordinates }) => {
  console.log('Received location:', coordinates);
//if cornidates are empty or lat and lng are empty then return error
  if (!coordinates || (!coordinates.lat && !coordinates.lng)) {
    console.error('Invalid location data:', coordinates);
    return <div>No location data available</div>;
  }

  const getHereMapUrl = (coordinates) => {
    const zoomLevel = 12;
    const width = 350;
    const height = 350;
    const apiKey =  'bIsX5VYMM71qtUp7dUTG3FHXVbZcT3WTPp-UzgxW2Ao';
    const marker = `&poi=${coordinates.lat},${coordinates.lng}`;
    
    return `https://image.maps.ls.hereapi.com/mia/1.6/mapview?c=${coordinates.lat},${coordinates.lng}&z=${zoomLevel}&apiKey=${apiKey}&w=${width}&h=${height}${marker}`;
  };
  //makes a custom api call with all the predeifined variables
  

  console.log('Map URL:', getHereMapUrl(coordinates));

  return (
    <div>
      <img src={getHereMapUrl(coordinates)} alt={""} />{/* calls API request and uses the return string for the picture */}
    </div>
  );
};

export default MapComponent;
