// PropertyDetails.js
import React, { useEffect, useState } from 'react';
import Carousel from 'react-bootstrap/Carousel';
import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs';
import { useParams } from 'react-router-dom';
import MapComponent from './MapComponent';

const PropertyDetails = ({properties}) => {
  const { propertyId } = useParams();
  const [property, setProperty] = useState(null);
  

  useEffect(() => {
    // Find the specific property using propertyId
    const selectedProperty = properties.find(prop => prop.id === propertyId);
    setProperty(selectedProperty);
    
  }, [propertyId, properties]);

 

  if (!property) {
    return <div>Loading...</div>;
  }//if property is empty return loading

  const floorPlan = property.floorPlan;//incorporating floorplan into the property details
  const floorPlanName = (floorPlan) => { floorPlan.split('/').pop(); // Assuming the path is in the format "images/imagename.jpg"
    }

   // const firstPropertyImage = property.picture[0];
    //console.log(firstPropertyImage);
   // const firstImageName = firstPropertyImage.split('/').pop(); // Assuming the path is in the format "images/imagename.jpg"
    const imageName = (image) => { image.split('/').pop(); }// Assuming the path is in the format "images/imagename.jpg"
    const isPropertyRent = property.purchaseType.toLowerCase() === "rent";

//process.ev.PUBLIC_URL is the public folder have to call with the image name
  return (
    <div className='propertyDescription'>
         <Carousel fade> {/* Bootstrap carousel to display images */}
            {property.picture.map((image) => (
            <Carousel.Item>
                <img src={`${process.env.PUBLIC_URL}/${image}`} alt={imageName(image)} /> {/* Have to add process env because in a different path was getting an error loading images */}
            </Carousel.Item> ))}
            
        </Carousel>
      
    <div className="shortDesc">        {/* displaying relevent data from JSON */}
        <h2>Description:</h2>
        <h3>Type: {property.type}</h3>
        {isPropertyRent ? (
          <h3>Price: {"" + property.price + " pcm"} </h3>
        ) : (
          <h3>Price: {"" + property.price} </h3>
        )}
        <h3>Bedrooms: {property.bedrooms}</h3>
        <h3>Location: {property.location}</h3>
        <h3>Postcode: {property.postcode}</h3>
        <h3>Added: {property.added.month} {property.added.day}, {property.added.year}</h3>
    </div> 
            {/* Using Tabs in Bootstrap to easily display the 3 tabss */}
            <Tabs
            defaultActiveKey="profile"
            id="uncontrolled-tab-example"
            className="mb-3"
            >
            <Tab eventKey="longDescription" title="Description">
                <div className="longDesc"><p>{property.description}</p></div>
                
            </Tab>
            <Tab eventKey="floorplan" title="FloorPlan">
            <div className="largeImageContainer">
                <img className='floorPlan' src={`${process.env.PUBLIC_URL}/${floorPlan}`} alt={floorPlanName} />
            </div>
            </Tab>
            <Tab eventKey="map" title="Map" >
                <MapComponent coordinates={property.coordinates}/> {/* coordinates passed to map component  */}
            </Tab>
            </Tabs>
    </div>
    
  );
};

export default PropertyDetails;
