// FavoriteList.js
import React from 'react';
import ImageCard from './ImageCard';

const FavoriteList = ({ favorites, handleAddToFavorites, handleRemoveFromFavorites, handleRemoveAllFromFavorites }) => {
  

  return (
    
    
    <div className='gallery'>
      <h2>Favorites List</h2>
      <a id='clear' onClick={handleRemoveAllFromFavorites}><h3>Clear Favorites</h3></a>{/* clear favs button */}
      <ul>
        
        {/* maping favorites list to individual properties and creating image cards in the favorites section. */}
        {favorites.map((property) => (
          <ImageCard key={property.id} favorites={favorites} property={property} handleAddToFavorites={handleAddToFavorites} handleRemoveFromFavorites={handleRemoveFromFavorites}/>
        ))}
        
      </ul>
    </div>
  );
};

export default FavoriteList;
